  width: 80px;
  background-color: rgb(243, 54, 51);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
  position: relative;
  display: inline-block;
}

.NCB_orange {
  width: 80px;
  background-color: rgb(254, 145, 52);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}
.NCB_yellow {
  width: 80px;
  background-color: Rgb(241, 232, 3);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}
.NCB_green {
  width: 80px;
  background-color: rgb(58, 145, 64);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}

.CB_red {
  width: 80px;
  background-color: rgb(130, 130, 34);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}

.CB_orange {
  width: 80px;
  background-color: rgb(176, 175, 37);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}
.CB_yellow {
  width: 80px;
  background-color: Rgb(231, 231, 3);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}
.CB_green {
  width: 80px;
  background-color: rgb(115, 115, 59);
  display: inline-block;
  height: 80px;
  border-radius: 20%;
  float: left;
  /* margin: 0 24px; */
}

.color-square .colorText {
  margin-top: 24px;
  opacity: 0;
  transition: 0.6s opacity;
}


#hover-square:hover > .colorText {
  opacity: 1;
}

.activity {
  max-width: 980px;
  margin: 0 auto;
}

.stat {
  border-radius: 24px;
  background-color: rgb(204, 204, 204);
  margin: 48px auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
}

.stat-count {
  text-align: right;
}

.color-p {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 35px;
}

.round-attempts {
  font-size: 20px;
}

.row {
  display: flex;
  justify-content: center;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

.button-container {
  width: 136px;
  margin: 0 auto;
}

.button {
  font-family: "IBM Plex Sans", sans-serif;
  background-color: rgb(194, 194, 194);
  border: none;
  border-radius: 20px;
  color: black;
  padding: 8px 20px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  align-items: center;
  transition: 0.5s;
  margin: 0 10px;
}

.button:hover {
  background-color: rgb(17, 108, 211);
  color: white;
  border: linear-gradient(to right, green, blue, indigo);
  /* opacity: .5; */
}

